@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;600&display=swap);
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

::-webkit-scrollbar {
    width: 10px;
}
::-webkit-scrollbar-track {
    background-color: #dddddd;
}
::-webkit-scrollbar-thumb {
    background-color: #666666;
    border-radius: 8px;
}
::-webkit-scrollbar-thumb:hover {
    background-color: #3a3a3a;
}
.Contact {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background-color: #F6F6F6;
    min-height: 580px;
    min-width: 1020px;
}
.Contact-titulo {
    height: 20px;
    font-size: 60px;
    color: #664E88;
    font-weight: bold;
    height: 20%;
}
.Contact-formulario {
    display: flex;
    width: 100%;
    padding: 32px;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    border-radius: 16px;
    height: 400px;
}

.Contact-formulario form {
    width: 600px;
    border: 2px solid #664E88;
    height: 100%;
    padding: 12px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    
}

.Contact-formulario form div {
    display: flex;
    width: 600px;
    justify-content: space-evenly;
}

.Contact-formulario form input {
    width: 160px;
    padding: 8px;
    border-radius: 8px;
    outline: none;
    border: 2px solid #e58a0a4f;
    background-color: #e58a0a4f;
    transition: ease .1s;
    font-size: 16px;
    color: #3a3a3a;
}

.Contact-wave {
    width: 100%;
    height: 150px;
    min-width: 1020px;
    overflow: hidden;
}

.Contact-formulario form textarea {
    border: 2px solid #e58a0a4f;
    font-size: 16px;
    color: #3a3a3a;
    background-color: #e58a0a4f;
    padding: 8px;
    outline: none;
    border-radius: 8px;
    resize: none;
    height: 200px;
    width: 400px;
    transition: ease-in-out .05s;
}

.Contact-formulario textarea:focus, form input:focus {
    border: 2px solid #664E88;
}

.Contact-formulario form button {
    padding: 8px;
    border-radius: 8px;
    font-weight: bold;
    outline: none;
    border: none;
    color: #F6F6F6;
    background-color: #E5890A;
    transition: ease .1s;
}

.Contact-formulario form button:hover {
    background-color: #664E88;
}

.img_svg {
    max-width: 500px;
    width:  100%;
    height: 100%;
    background-image: url(/bruiz-cv-web/static/media/undraw_contact_us_-15-o2.f0d97391.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100%;
}

.Contact-container {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 100%;
}

@media (max-width: 680px) {
    .Contact {
        height: 900px;
        min-width: 0;
    }
    .Contact-container {
        display: flex;
        height: 800px;
        align-items: center;
        justify-content: center;
    }
    .Contact-wave {
        min-width: 0;
    }
    .Contact-formulario form {
        width: 100%;
        height: 400px;
    }
    .Contact-formulario form textarea {
        width: 100%;
        margin: 4px;
    }
    .Contact-formulario-inputs {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .Contact-formulario form div {
        width: 100%;
    }
    .Contact-formulario form input {
        width: 280px;
    }
    .Contact-formulario-inputs input {
        margin: 4px;
    }
    .Contact-formulario {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 960px;
    }
    .img_svg {
        margin: 16px;
        width:100%;
        height: 200px;
    }
}
.Header {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: #F6F6F6;
    min-width: 1020px;
    min-height: 580px;
}
.Header-wave {
    position: absolute;
    min-width: 1020px;
    width: 100%;
    height: 150px;
    overflow: hidden;
}
.Header-wave-svg {
    height: 100%;
    width: 100%;
}
.Header-wave-path {
    stroke: none; fill: #664E88;
}
.Header-container {
    width: 100%;
    display: flex;
    margin-top: 40px;
    flex-direction: row;
    justify-content: space-between;
}

.Header-info {
    padding: 32px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    min-height: 525px;
    cursor: default;
}

.Header-info h1 {
    font-size: 72px;
    font-weight: bolder;
    color: #9D5C0D;
}

.Header-info span {
    margin: 0px;
    font-size: 20px;
    font-weight: bold;
    color: #3a3a3a;
}

.Header-info p {
    font-size: 16px;
    font-weight: bold;
    color: #3a3a3a;
}

.Header-info h2 {
    font-size: 32px;
    font-weight: bolder;
    color: #E5890A;
}

.Header-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    min-height: 525px;
}

.Header-wrapper-form {
    -webkit-clip-path: circle(50% at 50% 50%);
            clip-path: circle(50% at 50% 50%);
    background-image: url(/bruiz-cv-web/static/media/perfil.3cb70ae4.jpeg);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    width: 380px;
    height: 380px;
}

.Header-contenedor-links {
    display: flex;
    justify-content: space-between;
    width: 160px;
}

.Header-contenedor-links a {
    display: inline-block;
    padding: 2px;
    border-radius: 8px;
    border: 2px solid #E5890A;
    text-decoration: none;
    color: #E5890A;
    font-weight: bold;
    transition: ease .1s;
}

.Header-contenedor-links a:hover {
    background: #E5890A;
    color: #F6F6F6;
}

.Header-contenedor-links a:active {
    background: #9D5C0D;
    border-color: #9D5C0D;
    color: #F6F6F6;
}

@media (max-width: 680px) {
    .Header {
        min-width: 0;
        min-height: 760px;
        overflow: hidden;
    }
    .Header-wrapper {
        min-height: 0;
        height: 200px;
    }
    .Header-wrapper-form {
        margin-top: 62px;
        height: 140px;
        width: 140px;
    }
    .Header-container {
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin-top: 0;
    }
    .Header-wave {
        min-width: 0;
        position: absolute;
        height: 100px;
    }
    .Header-info {
        min-height: 0;
        height: 400px;
        margin-top: 102px ;
    }
    .Header-info span {
        text-align: center;
    }
    .Header-info h1 {
        text-align: center;
        font-size: 64px;
    }
    .Header-info h2 {
        text-align: center;
    }
    .Header-info p {
        text-align: center;
    }
    .Header-contenedor-links {
        width: 100%;
        justify-content: center;
    }
    .Header-contenedor-links a {
        margin: 0 8px;
    }
  }
.Skills {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    background-color: #664E88;
    min-height: 580px;
    min-width: 1020px;
    padding: 32px;
}
.Skills h2 {
    height: 20px;
    font-size: 60px;
    color: #F6F6F6;
    font-weight: bold;
    height: 20%;
}
.Skills-tarjeta {
    display: flex;
    padding: 10px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
    background-color: #F6F6F6;
    width: 200px;
    height: 400px;
    box-shadow: 0px 0px 31px -8px rgba(255, 255, 255, 0.75);
    transition: ease-in .1s;
    /* falta max width y el min */
}

.Skills-tarjeta h3 {
    color: #E5890A;
    text-align: center;
    font-size: 26px;
    font-weight: bold;
}

.Skills-contenedor-tarjetas {
    width: 100%;
    display: flex;
    flex-direction:  row;
    justify-content: space-evenly;
    align-items: center;
}

.Skills-tarjeta span {
    display: block;
    text-align: center;
    font-weight: bold;
    color: #3a3a3a;
}

.Skills-icono {
    font-size: 56px;
    color: #E5890A;
}

@media (max-width: 680px) {
    .Skills {
        min-width: 0;
        width: 100%;
        height: 1300px;
    }
    .Skills h2 {
        text-align: center;
    }
    .Skills-contenedor-tarjetas {
        flex-direction: column;
    }
    .Skills-tarjeta {
        margin: 8px;
        height: 240px;
    }
}
